
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

.task-col, .task-tag {
  float: left;
  margin-right: 5px;
}
.task-duration{
  float: right;
}
.task-start-time, .task-end-time{
  width: 45px;
  text-align: left;
  font-weight: bold;
}
.task-duration{
  width: 90px;
  text-align: right;
}
